export default {

	functional: true,

	name: "latte-moment",

	props: {
		format: {default: "d MMMM YYYY", type: String},
		ms: {default: () => Date.now(), type: Number}
	},

	render(h, {parent, props, _v})
	{
		return _v(parent.moment(props.ms).format(props.format));
	}

}

