export default {

	name: "latte-tab",

	props: {
		badge: {default: "", type: String},
		icon: {default: "", type: String},
		label: {default: "", type: String}
	},

	data()
	{
		return {
			active: false
		};
	},

	mounted()
	{
		this.$parent.updateChildren();
	},

	watch: {

		active()
		{
			this.$emit("active", this.active);
		}

	}

}

