const DASH_CAP = 69;

export default {

	name: "latte-progress",

	props: {
		isIndeterminate: {default: false, type: Boolean},
		isRing: {default: false, type: Boolean},
		max: {default: 100, type: Number},
		min: {default: 0, type: Number},
		value: {default: 0, type: Number}
	},

	data()
	{
		return {
			bar: 0,
			ring: 0
		};
	},

	mounted()
	{
		this.renderProgress();
	},

	computed: {

		barStyle()
		{
			if (this.isIndeterminate)
				return {};

			return {
				width: `${this.bar}%`
			};
		},

		ringStyle()
		{
			if (this.isIndeterminate)
				return "40 999";

			return `${this.ring} 999`;
		}

	},

	methods: {

		renderProgress()
		{
			if (this.isIndeterminate)
				return;

			const max = this.max - this.min;
			const value = Math.max(this.min, Math.min(this.max, this.value)) - this.min;
			const p = value / max;

			if (this.isRing)
				this.renderProgressRing(p);
			else
				this.renderProgressBar(p);
		},

		renderProgressBar(p)
		{
			this.bar = p * 100;
		},

		renderProgressRing(p)
		{
			this.ring = p * DASH_CAP;
		}

	},

	watch: {

		isIndeterminate()
		{
			this.renderProgress();
		},

		isRing()
		{
			this.renderProgress();
		},

		max()
		{
			this.renderProgress();
		},

		min()
		{
			this.renderProgress();
		},

		value()
		{
			this.renderProgress();
		}

	}

}

