export default {

	name: "latte-combo-box-item",

	props: {
		value: {required: true}
	},

	beforeDestroy()
	{
		this.$parent.unregisterOption(this);
	},

	data()
	{
		return {
			active: false
		};
	},

	mounted()
	{
		this.$parent.registerOption(this);
	},

	methods: {

		click()
		{
			this.$emit("select", this);
		}

	}

}

