const AVATAR_COLORS = [
	"#477d59",
	"#a54549",
	"#c1823f",
	"#3c9b9f",
	"#aa4b83",
	"#b3464b",
	"#e5993b",
	"#7f51a9"
];

export default {

	name: "latte-initials",

	props: {
		initials: {default: "BM", type: String}
	},

	data()
	{
		return {
			seed: 0
		};
	},

	mounted()
	{
		this.onInitialsChanged();
	},

	computed: {

		color()
		{
			return AVATAR_COLORS[this.seed % AVATAR_COLORS.length];
		}

	},

	methods: {

		onInitialsChanged()
		{
			let seed = 0;

			for (let i = 0; i < this.initials.length; i++)
				seed ^= this.initials.charCodeAt(i);

			this.seed = seed;
		}

	},

	watch: {

		initials()
		{
			this.onInitialsChanged();
		}

	}

}

