import { dispatch } from "../../js/core/action";
import { register, remove } from "../../js/ui/overlay";
import { applyZ } from "../../js/core/z";
import { raf } from "../../js/util/dom";
import { getMainElement } from "../../js/core";
import { popupClosed, popupOpened } from "../../js/core/popup";

export default {

	name: "latte-overlay",

	props: {
		name: {default: "", required: true, type: String},
		opened: {default: false, type: Boolean},
		responsive: {default: true, type: Boolean}
	},

	data()
	{
		return {
			isOpen: false,
			isVisible: false
		};
	},

	destroyed()
	{
		remove(this.name);
	},

	mounted()
	{
		register(this.name, this);

		if (this.$el.parentNode)
			this.$el.parentNode.removeChild(this.$el);

		getMainElement().appendChild(this.$el);

		if (this.opened)
			this.open(this.name);
	},

	computed: {

		overlayClasses()
		{
			const classes = [];

			if (this.isOpen)
				classes.push("is-open");

			if (this.responsive)
				classes.push("is-responsive");

			if (this.isVisible)
				classes.push("is-visible");

			return classes;
		}

	},

	methods: {

		close()
		{
			if (!this.isVisible)
				return;

			popupClosed();

			raf(() => this.isOpen = false);
			raf(() => this.isVisible = false, 270);

			dispatch("latte:overlay", {overlay: this, open: false});
			this.$emit("close", this);
		},

		open()
		{
			if (this.isVisible)
				return;

			popupOpened();

			raf(() =>
			{
				this.isVisible = true;

				raf(() =>
				{
					applyZ(z => this.$el.style.setProperty("z-index", z));
					this.isOpen = true;
				});
			});

			dispatch("latte:overlay", {overlay: this, open: true});
			this.$emit("open", this);
		}

	},

	watch: {

		name(n, o)
		{
			remove(o);
			register(n, this);
		},

		opened()
		{
			if (this.opened)
				this.open();
			else
				this.close();
		}

	}

}

